.admin-me-page {
  min-height: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 24px;
  color: var(--text-color) !important;
}

.admin-me-card {
  width: 100%;
  max-width: 520px;
  box-shadow: 0 8px 28px rgba(0, 0, 0, 0.12);
  border-radius: 14px;
  background: var(--background-color) !important;
  color: var(--text-color);

  .ant-form-item {
    margin-bottom: 16px;
  }
}
.admin-me-form {
  padding: 24px;
}

.admin-me-avatar {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 24px;

  .avatar-img {
    width: 96px;
    height: 96px;
    border-radius: 50%;
    object-fit: cover;
    margin-bottom: 12px;
  }

  .change-avatar-btn {
    cursor: pointer;
    color: var(--text-color);
    font-size: 14px;
  }
}

.ant-form-item-label > label {
  color: var(--text-color);
  font-weight: 600;
}
.ant-input-outlined {
  background: var(--background-color) !important;
  color: var(--text-color);
}
.ant-typography {
  color: var(--text-color) !important;
}
label {
  color: var(--text-color) !important;
}
