.custom-input-label {
  display: block;
  font-weight: 600;
  font-size: 14px;
  color: var(--text-color);
  margin-bottom: 6px;
}
.ant-input-outlined {
  background-color: var(--background-color);
  color: var(--text-color);
  border: 1px solid #555;
  border-radius: 4px;
  padding: 8px 12px;
  transition: border-color 0.3s;
  &::placeholder {
    color: var(--text-color);
  }
  &:hover {
    background-color: var(--background-color);
  }
  &:focus {
    background-color: var(--background-color);
  }
}
