.service-info {
  background-color: #1f1f1f;
  border: 1px solid #333;
  border-radius: 8px;
  padding: 16px 20px;
  margin-top: 20px;
  color: #e0e0e0;
  font-size: 14px;
  line-height: 1.6;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px 16px;

  p {
    margin: 0;
    display: flex;
    gap: 6px;
    white-space: nowrap;

    strong {
      color: #40a9ff;
      font-weight: 500;
      min-width: 140px;
    }
  }
}
